/* App.css */

/* Global Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  color: #333;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
}

/* Header Styles */
.header {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
}

.header h1 {
  font-size: 36px;
  margin: 0;
  letter-spacing: 2px;
}

.header p {
  font-size: 18px;
  margin: 10px 0;
  color: #ccc;
}

/* Navigation Styles */
.nav {
  background-color: #f9f9f9;
  padding: 10px 0;
}

.nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.nav li {
  margin: 0 10px;
}

.nav a {
  text-decoration: none;
  color: #555;
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav a:hover {
  color: #333;
}

/* Main Content Styles */
.main {
  margin-bottom: 30px;
}

.hero {
  background-color: #f9f9f9;
  padding: 100px 0;
  text-align: center;
}

.hero h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #333;
}

.hero p {
  font-size: 18px;
  color: #555;
}

.cta-button {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #555;
}

.features {
  background-color: #fff;
  padding: 50px 0;
  text-align: center;
}

.features h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.feature img {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}

.feature h3 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.feature p {
  font-size: 16px;
  line-height: 1.5;
  color: #555;
}

.pricing {
  background-color: #f9f9f9;
  padding: 50px 0;
  text-align: center;
}

.pricing h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

.pricing-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 20px;
  margin-bottom: 20px;
}

.pricing-option-header h3 {
  font-size: 24px;
  margin-bottom: 5px;
  color: #333;
}

.pricing-option-header p {
  font-size: 18px;
  color: #555;
}

.pricing-option ul {
  list-style-type: none;
  margin: 20px 0;
  padding: 0;
  text-align: left;
}

.pricing-option ul li {
  font-size: 16px;
  margin-bottom: 10px;
  color: #555;
}

/* Contact Styles */
#contact {
  background-color: #f9f9f9;
  padding: 50px 0;
  text-align: center;
}

#contact h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

#contact p {
  font-size: 16px;
  line-height: 1.5;
  color: #555;
}

form input,
form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button[type="submit"] {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #555;
}

/* Footer Styles */
.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 10px 0;
}

.footer p {
  margin: 0;
  font-size: 14px;
}